import request from "@/apis/http";
import { AxiosPromise } from "axios";
import {
  EdmCardResponse,
  EdmParams,
  EdmResponse,
  SendEdmParams
} from "@/typings/edm";

export function getEdms(): AxiosPromise<EdmCardResponse[]> {
  return request({
    url: "/edms/",
    method: "GET"
  });
}

export function getEdm(id: number | string): AxiosPromise<EdmResponse> {
  return request({
    url: `/edm/${id}/`,
    method: "GET"
  });
}

export function postCreateEdm(params: EdmParams): AxiosPromise<EdmResponse> {
  return request({
    url: "/edm/",
    method: "POST",
    data: params
  });
}

export function postUpdateEdm(
  id: number,
  params: EdmParams
): AxiosPromise<EdmResponse> {
  return request({
    url: `/edm/${id}/`,
    method: "PUT",
    data: params
  });
}
//
export function postDuplicateEdm(id: number | string): AxiosPromise<EdmResponse> {
  return request({
    url: `/edm/${id}/duplicate/`,
    method: "POST"
  });
}


export function sendEdm(params: SendEdmParams): AxiosPromise<EdmResponse> {
  return request({
    url: `/edm/send/`,
    method: "POST",
    data: params
  });
}
