import axios from "axios";
import {
  findRefreshToken,
  findToken,
  removeRefreshToken,
  removeToken,
  setRefreshToken,
  setToken
} from "@/utils/tokenStorage";
// import store from "../../store";
import router from "@/router";
import { postTokenRefresh } from "@/apis/auth";

axios.defaults.baseURL = "https://weaverbird.bilabasia.com/api";
// axios.defaults.baseURL = "http://140.112.110.129:7777/api";

axios.interceptors.request.use(
  config => {
    const token = findToken();

    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    return config;
  },
  error => error
);

axios.interceptors.response.use(
  config => config,
  ({ response }) => {
    if (!response || response.status !== 401) {
      return Promise.reject(response);
    }

    // TODO Disabled Refresh Token. Reopen 
    if (response.data.code === "token_not_valid") {
      removeToken();
      removeRefreshToken();
      router.push({ name: "Login" });
    }
    return Promise.reject(response);

    /*
     * When response code is 401, try to refresh the token.
     * Eject the interceptor so it doesn't loop in case
     * token refresh causes the 401 response
     */
    // const refresh = findRefreshToken();
    // if (!refresh) {
    //   return Promise.reject(response);
    // }

    // return postTokenRefresh({ refresh })
    //   .then(({ data }) => {
    //     setToken(data.access);
    //     setRefreshToken(data.refresh);
    //     return axios(response.config);
    //   })
    //   .catch(async error => {
    //     console.warn("refresh error", error);
    //     removeToken();
    //     removeRefreshToken();
    //     return Promise.reject(error);
    //   });
  }
);

export default axios;
